body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    
}
.rsm-geographies{
    cursor:pointer;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.type-dark {
    color: #222 !important;
    background: #f9f9f9 !important;
    border: 1px solid #333;
    width: 170px !important;
}

.type-dark::after{
    border-top-color:#f9f9f9 !important;
}

.form-inline{
    justify-content: center;
    -webkit-justify-content: center;
    -ms-flex-pack:center;
}

.search {
  width: 30rem;
  margin: 3rem auto 1rem auto;
  text-align: right;
  position: relative;
  bottom:25px;
  z-index: 99;
}
.search-box {
  border: 4px solid transparent;
  border-radius: 2px;
  font-size: 2rem;
  width: 100%;
  padding: .5rem;
  transition: width 0.3s;
}
.search-box:focus {
  width: 100%;
  outline: none;
  border: 4px solid #08a1b6;
}
.search-btn {
  height: 100%;
  width: 3em;
  margin-top: -1.7em;
  position: absolute;
  top: 50%;
  right: 0.5rem;

  opacity: 0.2;
  background-color: transparent;
  border: 0;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("data:image/png;base64,");
}
.search-btn:hover {
  outline: none;
  opacity: 0.4;
  cursor: pointer;
}
.search-btn:focus {
  outline: none;
  opacity: 0.6;
}

ul.options {
  display: block;
  list-style: none;
  width: 30rem;
  transition: width 0.3s;
  margin: auto;
  position: relative;
}

ul.options li {
  display: block;
  background: white;
  margin: 10px auto;
  padding: 10px;
  font-size: 1.2rem;
  width: 100%;
  border-radius: 2px;
}
ul.options li:hover {
  font-weight: bold;
  color: #00b4cc;
  cursor: pointer;
  transition: 0.3s all;
}

ul.options li.option-active {
  background: whitesmoke;
  font-size: 1.5rem;
  color: #00b4cc;
}
.no-options {
  color: white;
}

.windowView {
    width:75%;
    position:fixed;
    z-index: 9;
    top:25%;
    left: 50%;
    transform: translate(-50%, 20%);
    background: rgba(255,255,255, .65);
    padding: 10px;
}

ul.options{
    position:relative;
    z-index:99;
}
.h3{
    font-size:3em;
    color:#aba6ad;
    font-weight:600;
    margin-top:30px;
} 

.rsm-geography:focus, .rsm-geography:hover{
   fill: rgb(255 3 143) !important;
}